import app from 'flarum/admin/app';
import ExtensionPage from 'flarum/admin/components/ExtensionPage';
import QueueSection from './QueueSection';
import ControlSection from './ControlSection';
export default class SettingsPage extends ExtensionPage {
  sections(vnode) {
    const items = super.sections(vnode);
    items.setPriority('content', 10);
    items.add('control', m(ControlSection, null), 8);
    if (parseInt(app.data.settings['flarum-package-manager.queue_jobs'])) {
      items.add('queue', m(QueueSection, null), 5);
    }
    items.setPriority('permissions', 0);
    return items;
  }
}
flarum.reg.add('flarum-package-manager', 'admin/components/SettingsPage', SettingsPage);